<template>
    <div class="myRecom">
        <!-- 顶部导航 -->
        <van-nav-bar
            title="每日推荐"
            left-arrow
            :border="false"
        >
        <template #left>
             <span class="iconfont icon-arrow-fine-up-copy" @click.stop="onClickLeft"></span>
        </template>
        </van-nav-bar>
      

        <van-pull-refresh
            v-model="isLoading"
            success-text="刷新成功"
            @refresh="onRefresh"
            >

            <!-- 排行榜 -->
            <div class="melody">
                <div class="single" v-for="(item,index) in nowSingle" :key="index" @click="ff_songs(item.id,item.name,item.picUrl,item.song.artists,true)" >
                     <van-skeleton :round="true" avatar-shape="square"  avatar  avatar-size=65px  :row="3" :loading="loading">
                            <img v-lazy="item.picUrl"/>
                     </van-skeleton>
                    
                   
                    <div class="singleBodys">
                        <van-skeleton title row-width=100%  :row="2" :loading="loading">
                            <p>{{item.name}}</p>
                            <p class="astr">
                                <span>SQ</span>
                                <span>无损</span>
                                <span>-{{item.song.artists | ff_names}}</span>
                            </p>
                        </van-skeleton>
                    </div>
                    <van-skeleton title row-width=100%  :row="2" :loading="loading">
                         <span class="iconfont icon-sandian"></span>
                    </van-skeleton>
                   
                </div>
            </div>


        </van-pull-refresh>
        
    </div>
</template>

<script>
    import { mapState } from 'vuex';
    import '../assets/less/Recom.less'
    export default {
        name : 'Recom',
        data() {
            return {
                singles   : [],
                nowSingle : [],
                isLoading : false,
                end       : 8,
                stats     : 0,
                loading   : true,
            }
        },
        methods: {
            onClickLeft(){
                this.$router.go(-1);
            },
             // 推荐新音乐
            ff_songData(){
               this.axios({
                    url : '/personalized/newsong',
                    method : 'GET',
                    params : {
                        limit : 50
                    }

               })
               .then(res=>{
                //    console.log(res.data.result)
                   if(res.data.code == 200){
                         this.$toast.clear();
                         res.data.result.map((item,index)=>{
                             if(index > 12){
                                 this.singles.push(item)
                             }
                         })
                         this.nowSingle = this.singles.slice(0,8);
                          console.log(this.singles)
                          setTimeout(()=>{
                              this.loading = false;
                          },500)
                          
                         
                        
                   }
                   
               })
               .catch(err=>{
                   console.log(err)
               })
            },
             //  下拉刷新
            onRefresh() {
                this.loading = true;
            setTimeout(() => {
                this.end   = this.end >= 38 ?  0 : this.end;
                this.stats = this.end ;
                this.end   = this.end + 8;
                
                this.nowSingle = this.singles.slice(this.stats,this.end);
                console.log(this.nowSingle)
              
                this.$toast('刷新成功');
                this.isLoading = false;
                this.loading   = false;
            }, 1000);
            },
            // 获取歌曲url
            ff_songs(id,name,imgUrl,autos,isSingle=false,Brief=null){
                 console.log(id,name,imgUrl,autos)
                 let auto = '';
                 autos.forEach(item=>{
                    auto += `${item.name}/`
                 })
                 auto = auto.slice(0,-1);

                 let  musicObj = {};
                 this.axios({
                     url : '/song/url',
                     method : 'GET',
                     params : {
                         id
                     }
                 })
                 .then(res=>{
                     console.log(res)
                     if(res.data.code == 200){
                           musicObj.name     = name;
                           musicObj.url      = res.data.data[0].url;
                           musicObj.imgUrl   = imgUrl;
                           musicObj.id       = id;
                           musicObj.isFlash  = true;
                           musicObj.isPlay   = true;
                           this.$store.commit('gaiMusic',musicObj);


                           // 判断是否单曲加入队列
                         if(isSingle){
                             let ispduan =  true;
                             this.queue.forEach((Item,index)=>{
                                 if(Item.songId == id){
                                       ispduan = false;
                                       this.queue.splice(index,1);
                                       this.queue.unshift({
                                            songId    : id,
                                            songName  : name,
                                            songUrl   : imgUrl,
                                            autName   : auto,
                                            songBrief : Brief,
                                        })

                                 }
                             });


                            if(ispduan){
                                this.queue.unshift({
                                    songId    : id,
                                    songName  : name,
                                    songUrl   : imgUrl,
                                    autName   : auto,
                                    songBrief : Brief,
                                })
                               
                                let a =  this.queue.slice(0,30);
                                this.$store.dispatch('gaiQueue', a)

                            }
                           
                             sessionStorage.setItem('queue',JSON.stringify(this.queue))
                             this.isSingle = false;

                         }
                     }
                 })
                 .catch(err=>{
                     console.log(err)
                 })
            }
        },
        created(){
             this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
                loadingType: 'spinner',
            });
             // 推荐新音乐
            this.ff_songData();
        },
        computed:{
            ...mapState({
                queue : 'queue'
            })
        },
        filters:{
            ff_names(val){
                let str = '';
                val.forEach(item=>{
                    str += `${item.name} / `
                })
                return  str.slice(0,-3);
            }
        }
    }
</script>

<style lang="less" scoped>

</style>